.footer {
    height: 35vh;
    background-color: lightskyblue;
    display: flex;
    align-items: center;
}

.container {
    width: 80%;
    border: 1px solid;
    flex-direction: column;
    align-items: center;
}
.title {
    background-color: darkgrey;
}
.block {
    min-width: 270px;
    display: flex;
    justify-content: space-between;
}
.fragment {
    width: 50px;
    height: 50px;
    background-color: #e3c6f6;
}

.copyright {
   background-color: white;
}